import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getListDiscipline, deleteDisciplineById } from 'services/AddDiscipline';
import { getQueryParams } from 'utils/urlUtil';
import FormFilterDiscipline from './FormFilterDiscipline';

class AddDisciplinePage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
  };
  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
      reload: this.state.reload + 1,
    });
  };

  handleDelete = async id => {
    await deleteDisciplineById(id);
    message.success('Xa thnh cng');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {
    const { reload, paramSearchClass } = this.state;
    const columns = [
      {
        title: 'M SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'S Q',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngy Q',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
      },
      {
        title: 'Cp k lut',
        dataIndex: 'parentBonusTypeLabel',
        key: 'parentBonusTypeLabel',
      },
      {
        title: 'Ni dung vi phm',
        dataIndex: 'disciplineBehaviorLabel',
        key: 'disciplineBehaviorLabel',
      },
      {
        title: 'Hnh thc x l',
        dataIndex: 'disciplineLabel',
        key: 'disciplineLabel',
      },
      {
        title: 'Ni dung x l',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#discipline/updateForm/${record.approvalId}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
            hidenBtnDelete="true"
          />
        ),
      },
    ];
    return (
      <Card title="Nhp k lut">
        <Row>
          <Col span={24}>
            <FormFilterDiscipline dataForm={this.dataFormSeach} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/discipline/addForm" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getListDiscipline}
              paramSearch={paramSearchClass}
              columns={columns}
              rowKey={(record) => record.approvalId + record.id}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddDisciplinePage />
  </Page>
);



